*, :before, :after {
  box-sizing: inherit;
  margin: 0;
  padding: 0;
}

html {
  box-sizing: border-box;
  font-size: 62.5%;
  overflow-x: hidden;
}

body {
  direction: rtl;
  color: #090b1f;
  font-family: IRANYekanX, Tahoma, sans-serif;
  font-weight: normal;
  line-height: 1.6;
  overflow-x: hidden;
}

.exclude {
  color: #2c359c;
}

.hero {
  color: #f8f9fa;
  background-image: radial-gradient(circle, #ffac66, #ff9033 40%, #ffac66);
  background-repeat: no-repeat;
  background-size: 100% 100%;
  grid-template-columns: 1fr 1fr 4fr 1fr 1fr;
  place-items: start center;
  gap: 2rem;
  height: 100%;
  padding: 10rem 2rem 0;
  display: grid;
  overflow: hidden;
}

.hero__text-box {
  text-align: center;
  grid-column: 1 / -1;
}

.hero__image-box {
  grid-column: 3 / 4;
  align-self: stretch;
  width: 70%;
  position: relative;
}

.hero__image {
  width: 100%;
  display: block;
}

.hero__svg {
  fill: #fff;
  width: 100%;
  line-height: 0;
  position: absolute;
  bottom: -1px;
  left: 0;
  overflow: hidden;
}

.hero__icon-box {
  flex-direction: column;
  justify-content: space-around;
  width: 100%;
  height: 100%;
  display: flex;
}

.hero__icon-box:first-of-type {
  grid-area: 2 / 1 / 3 / 3;
  align-items: end;
}

.hero__icon-box:last-of-type {
  grid-area: 2 / 4 / 3 / 6;
  align-items: start;
}

.airplane {
  z-index: 10;
  backface-visibility: hidden;
  height: 8rem;
  animation: 15s linear infinite moveLeft;
  position: absolute;
  top: 60%;
  transform: translateY(-50%);
}

.airplane__image {
  height: 100%;
  display: block;
}

@keyframes moveLeft {
  0% {
    right: -30rem;
    transform: translateY(-50%);
  }

  50% {
    right: 50%;
    transform: translateY(-50%);
  }

  100% {
    right: calc(100% + 50rem);
    transform: translateY(-50%);
  }
}

@keyframes rotate {
  0% {
    transform: rotate(0);
  }

  50% {
    transform: rotate(180deg);
  }

  100% {
    transform: rotate(360deg);
  }
}

.header {
  z-index: 1;
  background-color: #0000;
  grid-column: full-start / full-end;
  justify-content: space-between;
  align-items: center;
  width: 100vw;
  height: 10rem;
  padding: 2rem 4rem;
  font-size: 1.8rem;
  display: flex;
  position: absolute;
  top: 0;
  left: 0;
}

.header__link {
  align-items: center;
  gap: 1rem;
  font-size: 1.8rem;
  text-decoration: none;
  display: flex;
}

.header__link:visited, .header__link:link {
  color: #28308c;
}

.header__logo {
  height: 8rem;
  display: block;
}

.nav__list {
  gap: 4rem;
  list-style: none;
  display: flex;
}

.nav__link:link, .nav__link:visited {
  color: #212529;
  text-decoration: none;
}

